<template>
    <!-- <Table :columns="columns" :data="data"></Table> -->
    <div class="tables">
        <div class="tHead">
            <Table :columns="columns" border></Table>
        </div>
        <div class="tBody">
            <!-- <GeminiScrollbar class="my_scroll" :autoshow="true"> -->
                <Table :columns="columns"
                       :data="data"
                       border></Table>
            <!-- </GeminiScrollbar> -->
        </div>
    </div>
</template>

<script>
export default {
    props:{
        columns:{
            type:Array
        },
        data:{
            type:Array
        }
    }
}
</script>

<style lang="scss">
.tables{
    width:100%;
    height:100%;
    .tHead{
        height:40px;
        background:red;
        .ivu-table-wrapper{
            .ivu-table{
                background-color:rgba(255,255,255,0);
                &::before{
                    height:0;
                }
                .ivu-table-header{
                    table{
                        thead{
                            tr{
                                th{
                                    background-color:rgba(0,0,0,0.48);
                                    .ivu-table-cell{
                                        color:#b3ebff;
                                    }
                                }
                            }
                        }
                    }
                }
                .ivu-table-body{
                    
                }
                .ivu-table-tip{
                    display:none;
                }
            }
        }
    }
    .tBody{
        height:calc(100% - 40px);
        overflow: hidden;
        .my_scroll{
            .gm-scroll-view{
                .ivu-table-wrapper{
                    .ivu-table{
                        background-color:rgba(255,255,255,0);
                        .ivu-table-body{
                            table{
                                tbody{
                                    tr{
                                        td{
                                            border-color:#01305a;
                                            background-color:rgba(0,0,0,0.16);
                                            .ivu-table-cell{
                                                color:#b3ebff;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        .ivu-table-tip{

                        }
                    }
                }
            }
        }
    }
}
</style>



